import React, { useState, useEffect } from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/main-layout-ja';
import Seo from '../components/seo';

//Image
import ListBg from '../images/bg/list.svg';
import ListBgSp from '../images/bg/list-sp.svg';
import PromotionCouncilDefaultImage from '../images/thumb/promotion_council.png';

const PromotionCouncilPage = ({ data }) => {
  const currentDate = new Date().toISOString();
  const promotionString = '推進協議会';
  data = data.allMicrocmsPosts.edges.filter(
    ({ node }) => node.public_date_ja && node.public_date_ja <= currentDate && node.category[0] === promotionString
  );
  data.forEach(function(article){
    const date = new Date(article.node.public_date_ja);
    const formattedDate = date.toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
    });
    const desiredFormat = formattedDate.replace(/(\d+)\/(\d+)\/(\d+)/, '$1/$2/$3');
    article.node.public_date_formatted = desiredFormat;
  });

  const allNews = data;
  // State for the list
  const [list, setList] = useState([...allNews.slice(0, 15)]);

  // State to trigger oad more
  const [loadMore, setLoadMore] = useState(false);

  // State of whether there is more to load
  const [hasMore, setHasMore] = useState(allNews.length > 15);
  
  // Load more button click
  const handleLoadMore = () => {
    setLoadMore(true);
  };

  //Check if there is more
  useEffect(() => {
    const isMore = list.length < allNews.length;
    setHasMore(isMore);
  }, [list]); //eslint-disable-line

  // Handle loading more articles
  useEffect(() => {
    if (loadMore && hasMore) {
      const currentLength = list.length;
      const isMore = currentLength < allNews.length;
      const nextResults = isMore
        ? allNews.slice(currentLength, currentLength + 15)
        : [];
      setList([...list, ...nextResults]);
      setLoadMore(false);
    }
  }, [loadMore, hasMore]); //eslint-disable-line

  return <Layout>
    <section className="list">
      <picture>
          <source media="(min-width: 768px)" srcSet={ListBg} />
          <img className="list__bg" src={ListBgSp} alt="お知らせ" />
      </picture>
      <div className="default-max-width">
          <h2 className="heading heading--no-line">Trusted Web推進協議会</h2>
          <p className="list__description">有識者とオープンに議論しているのが「推進協議会」です。</p>
          <div className="list__articles">
            <ul className="article-list">
              {list.map(edge => {
                  const article = edge.node;
                  const detailLink = article.url
                    ? article.url
                    : "/news/" + article.id;
                  const target = article.url
                    ? '_blank'
                    : null;
                  let description;

                  if (article.body_ja) {
                    const lengthLimit = 50;
                    const regex = /(<([^>]+)>)/ig;
                    const result = article.body_ja.replace(regex, '');

                    description = result.substring(0, lengthLimit)
                    if (result.length > 50) {
                      description = description + '...';
                    }
                  }

                  return (
                  <React.Fragment key={article.id}>
                    <li className="article-item">
                      <article className="article">
                        <a className="article__link" href={ detailLink } target={target}>
                          <span className="article__date" suppressHydrationWarning={true}>{article.public_date_formatted}</span>
                          <div className="article__image-wrapper">
                            {
                              article.thumbnail_image
                                ? <img className="article__img" src={article.thumbnail_image.url} alt={ article.title_ja } />
                                : <img className="article__img" src={PromotionCouncilDefaultImage} alt={ article.title_ja } />
                            }
                          </div>
                          <h3 className="article__title">{ article.title_ja }</h3>
                          <p className="article__description">{ description } </p>
                          <div className="article__tag-wrapper flex-wrap">
                            {
                              article.tags.map(tag => {
                                return (
                                <React.Fragment key={tag}>
                                  <span className="tag">{tag}</span>
                                </React.Fragment>
                                );
                              })
                            }
                          </div>
                        </a>
                      </article>
                    </li>
                  </React.Fragment>
                  )
              })}
            </ul>
          </div>
          <div className="text-center">
            {
              hasMore
                ? <button className="button" onClick={handleLoadMore}>もっと見る</button>
                : null
            }
          </div>
      </div>


    </section>
  </Layout>
}

export const query = graphql`
  {
    allMicrocmsPosts(
      sort: { fields: [public_date_ja, createdAt], order: DESC }
    ) {
      edges {
        node {
          id
          title_ja
          body_ja
          public_date_ja
          category
          tags
          createdAt
          thumbnail_image {
            url
            height
            width
          }
          url
        }
      }
    }
  }
`

export default PromotionCouncilPage

export const Head = () => (
  <Seo title='Trusted Web推進協議会' />
)